    dealDpHkfxTableData () {
      const list = []
      let dataList = []
      if (this.dphkfxIndex === 0) {
        dataList = this.dpHkfxTableDataInit.resM || []
      } else if (this.dphkfxIndex === 1) {
        dataList = this.dpHkfxTableDataInit.resJ || []
      } else if (this.dphkfxIndex === 2) {
        dataList = this.dpHkfxTableDataInit.resNl || []
      } else if (this.dphkfxIndex === 3) {
        dataList = this.dpHkfxTableDataInit.resY || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.dp = item.customer_name
        obj.mb = formatNumber(initNum(item.re_target_amt), 2)
        obj.hk = formatNumber(initNum(item.re_amt), 2)
        obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
        obj.tb = (initNum(item.re_amt_yoy) * 100).toFixed(1)
        obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
        list.push(obj)
      }
      const headerList = [
        {name: '店铺', prop: 'dp', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '目标', prop: 'mb', needOrder: true, order: '2', isPresent: false, needArrow: false},
        {name: '回款', prop: 'hk', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '达成', prop: 'dc', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: false},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ]
      this.dpHkfxTableData = {
        tableHeader: headerList,
        tableContent: list.sort(compareSort('mb', false))
      }
      // 设置第一行被选中， 从而查询下面线图和表格数据
      // 获取第一条数据，查询下面的线图和店铺表格数据
      if (this.dpHkfxTableData.tableContent.length > 0) {
        const firstName = this.dpHkfxTableData.tableContent[0].dp
        this.selectedDpName = firstName
        this.dpQsfxTitle = '趋势分析-' + firstName
        this.dpYdjdTitle = '月度进度-' + firstName
        // 这里要设置表格第一行默认选中
        this.$eventBus.$emit('resetSelectedRow', {type: 'payback-dp-hkfx', name: firstName})
        // 店铺月度进度
        this.getDpYdjdTableData(firstName)
        // 店铺趋势分析
        this.getDpQsfxTableData(firstName)
      } else {
        this.dpQsfxTitle = '趋势分析'
        this.dpYdjdTitle = '月度进度'
        this.dpLineData = []
        this.dpQsfxLineData = []
      }
    },
