    changeAllType ({btn, index}) {
      if (index === 0) {
        this.qsfxIndex = 0
        this.dpqsfxIndex = 0
        this.showZy = true
      } else {
        this.qsfxIndex = 0
        this.dpqsfxIndex = 0
        this.showZy = false
      }
    },
    tabsType ({activeValue, name, type, index}) {
      if (type === 'payback-zgs-hkfx') {
        this.hkfxIndex = index
        this.dealZgsHkfxTableData()
      } else if (type === 'payback-zgs-qsfx') {
        this.qsfxIndex = index
        this.dealZgsQsfxLineData()
      } else if (type === 'payback-zgs-dppm') {
        this.zgsdppmIndex = index
        this.dealDppmTableData()
      } else if (type === 'payback-dp-hkfx') {
        this.dphkfxIndex = index
        this.dealDpHkfxTableData()
      } else if (type === 'payback-dp-qsfx') {
        this.dpqsfxIndex = index
        this.dealDpQsfxLineData()
      }
    },
    changeTableRow ({row, name, type}) {
      if (type === 'payback-dp-hkfx') {
        this.dpQsfxTitle = '趋势分析-' + name
        this.dpYdjdTitle = '月度进度-' + name
        this.selectedDpName = name
        // 店铺月度进度
        this.getDpYdjdTableData(name)
        // 店铺趋势分析
        this.getDpQsfxTableData(name)
      }
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'payback-zgs-dppm') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.dppmTableData)
      } else if (type === 'payback-pbfx') {
        tempTable = _.cloneDeep(this.hkfxTableData)
      } else if (type === 'payback-dp-hkfx') {
        tempTable = _.cloneDeep(this.dpHkfxTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'payback-zgs-dppm') {
        // 这个来分辨哪个表格需要排序
        this.dppmTableData.tableHeader = headerList
        this.dppmTableData.tableContent = tableContent
      } else if (type === 'payback-pbfx') {
        this.hkfxTableData.tableHeader = headerList
        this.hkfxTableData.tableContent = tableContent
      } else if (type === 'payback-dp-hkfx') {
        this.dpHkfxTableData.tableHeader = headerList
        this.dpHkfxTableData.tableContent = tableContent
      }
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '回款')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    if (this.dateStr !== '') {
      this.getAllData()
    }
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '子公司', URL: '回款'})
    this.$eventBus.$on('userSelected', () => {
      this.getAllData()
    })
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  }
}
</script>
